import { useEffect, useRef } from "react";
import { fabric } from "fabric";
import ColorPicker from "./ColorPicker";
import {
    TriangleIcon, CircleIcon
    , RectangleIcon
} from "../icons/icons";
import * as common from "./common"
import styles from "./Submenu.module.css"

export default function FigureSubmenu({ canvas, addLayerItem }) {
    
    const figureList = ['triangle', 'rect', 'circle', 'image']
    const color = useRef('#000000');
    var figure, rect, circle, triangle, isDown, origX, origY;

      
        function addRect() {
          const rect = new fabric.Rect({
            width: 100,
            height: 100,
            noScaleCache: true,
            angle: 0,
            fill: 'black',
            type: 'rect',
            id: ++canvas.objectNum,
          });
          canvas.add(rect);
          canvas.centerObject(rect)
          addLayerItem(canvas, rect.toDataURL())
          canvas.setActiveObject(rect)
          common.addLayer(canvas,rect);
        }
      
        function addCircle() {
          const circle = new fabric.Circle({
            width: 100,
            height: 100,
            radius: 50,
            noScaleCache: true,
            angle: 0,
            fill: 'black',
            type: 'circle',
            id: ++canvas.objectNum,
          });
          canvas.add(circle);
          canvas.centerObject(circle)
          addLayerItem(canvas, circle.toDataURL())
          canvas.setActiveObject(circle)
          common.addLayer(canvas,circle);
          
        }
      
        function addTriangle() {
          const triangle = new fabric.Triangle({
            width: 100,
            height: 100,
            noScaleCache: true,
            angle: 0,
            fill: 'black',
            type: 'triangle',
            id: ++canvas.objectNum,
          });
          canvas.add(triangle);
          canvas.centerObject(triangle)
          canvas.setActiveObject(triangle)
          common.addLayer(canvas,triangle)
        }
      
        function addTextBox() {
          var textbox = new fabric.Textbox(' ', {
            width: 250,
            id: ++canvas.objectNum,
            fill: 'white',
            type: 'textbox'
          });
          canvas.add(textbox);
          canvas.centerObject(textbox)
          addLayerItem(canvas, textbox.toDataURL())
          canvas.setActiveObject(textbox)
          common.addLayer(canvas,textbox)
        }
      
        function addLocalImage(e) {
          e.target.value = ''
          canvas.isDrawingMode = false;
          
          document.getElementById("add-local-image").onchange = function (e) {
              var reader = new FileReader();
              reader.onload = function (e) { 
                  var image = new Image();
                  image.src = e.target.result;
                  image.onload = function () {
                      var img = new fabric.Image(image);
                      img.set({
                          id: ++canvas.objectNum,
      
                      });
      
                      if (img.width > canvas.width || img.height > canvas.height) {
                          var ratio = img.height/img.width;
                          img.scaleToWidth(canvas.width / 2);
                          img.scaleToHeight(canvas.width / 2 * ratio);
                      }
      
      
                      canvas.add(img).setActiveObject(img);
                      common.updateStates(canvas);
                      canvas.setActiveObject(img);
                      common.addLayer(canvas,img)
                      
                    }
                  }
                  reader.readAsDataURL(e.target.files[0]); // dataURL 
                }
      }

    function setFigureWidth(e) {
        var objects = canvas.getActiveObjects();
        var value;
        objects.forEach(object => {
            if (!e.target.value) value = 10
            else value = parseInt(e.target.value);
            object.set('width', value)
            canvas.renderAll();
        })
    }

    function setFigureHeight(e) {
        var objects = canvas.getActiveObjects();
        var value;
        objects.forEach(object => {
            if (!e.target.value) value = 10
            else value = parseInt(e.target.value);
            object.set('height', value)
            canvas.renderAll();
        })
    }

    function activateInput(e) {
        window.onkeydown = null;
        e.target.disabled = false;
    }

    function deactivateInput() {
        document.getElementById('figure-width').disabled = true;
        document.getElementById('figure-height').disabled = true;
        window.onkeydown = (e) => common.keyDownEvent(canvas, e);
    }


    var flag = false;
    var unselectableObject = null;

    function addElement(select) {

        common.mouseEventOff(canvas);

        canvas.defaultCursor = 'crosshair';

        canvas.on('mouse:down:before', (e) => {
            flag = true;
            if (e.target && flag) {
                canvas.discardActiveObject();
                e.target.selectable = false;
                unselectableObject = e.target
            }

            canvas.on({
                'mouse:down': (o) => {
                    isDown = true;
                    var pointer = canvas.getPointer(o.e);
                    origX = pointer.x; 
                    origY = pointer.y; 
                    if (select === "rect") {
                        rect = new fabric.Rect({
                            left: origX,
                            top: origY,
                            originX: 'left',
                            originY: 'top',
                            width: pointer.x - origX,
                            height: pointer.y - origY,
                            noScaleCache: true,
                            angle: 0,
                            fill: color.current,
                            type: 'rect',
                            id: ++canvas.objectNum,
                        });
                        figure = rect;
                    } else if (select === "circle") {
                        circle = new fabric.Circle({
                            left: origX,
                            top: origY,
                            originX: 'left',
                            originY: 'top',
                            radius: (pointer.x - origX) / 2,
                            fill: color.current,
                            id: ++canvas.objectNum,
                            type: 'circle'
                        });
                        figure = circle;
                    } else if (select === "triangle") {
                        triangle = new fabric.Triangle({
                            left: origX,
                            top: origY,
                            originX: 'left',
                            originY: 'top',
                            width: pointer.x - origX,
                            height: pointer.y - origY,
                            angle: 0,
                            fill: color.current,
                            id: ++canvas.objectNum,
                            type: 'triangle',
                        });
                        figure = triangle;
                    }
                    canvas.add(figure);
                    
                    canvas.setActiveObject(figure)
                },

                'mouse:move': (o) => {
                    if (!isDown) return;
                    var pointer = canvas.getPointer(o.e);

                    if (origX > pointer.x) {
                        figure.set({ left: Math.abs(pointer.x) });
                    }
                    if (origY > pointer.y) {
                        figure.set({ top: Math.abs(pointer.y) });
                    }

                    if (select === "circle") {
                        figure.set({ radius: Math.abs(origX - pointer.x) / 2 });
                    } else {
                        figure.set({ width: Math.abs(origX - pointer.x) });
                        figure.set({ height: Math.abs(origY - pointer.y) });
                    }
                    common.inputFigureInfo(figure)
                    canvas.renderAll();
                },

                'mouse:up:before': () => {
                    var objects = canvas.getObjects()
                    objects.forEach(object => { object.selectable = false });
                },

                'mouse:up': () => {
                    canvas.defaultCursor = 'default';
                    document.getElementById('figure-width').value = Math.round(figure.width);
                    document.getElementById('figure-height').value = Math.round(figure.height);

                    flag = false;
                    if (unselectableObject && unselectableObject.main !== true) unselectableObject.selectable = true;
                    var objects = canvas.getObjects();
                    objects.forEach(object => {
                        if (object.main !== true)
                            object.selectable = true
                    })
                    common.updateStates(canvas);
                    common.mouseEventOff(canvas);
                    common.addLayer(canvas,figure)
                    common.colorActiveLayer(canvas)
                    addLayerItem(canvas,figure.toDataURL())

                }
            })

        })

    }
    return (
        <div id='figure-menu' className={styles.Submenu}>
            <div className={styles.layout}>
            <p>
                <RectangleIcon onClick={addRect} />
                <CircleIcon onClick={addCircle} />
                <TriangleIcon onClick={addTriangle} />
            </p>
            <p><label></label> 
           <input id='figure-width' onClick={activateInput} onSelect={activateInput} onChange={setFigureWidth} type="text" />
            </p>
            <p><label></label> 
            <input id='figure-height' onClick={activateInput} onSelect={activateInput} onChange={setFigureHeight} type="text" /></p>
            <p><label></label>
            <ColorPicker canvas={canvas} addLayerItem={addLayerItem} color={color} />
            </p>  
            </div>
        </div>
    )
}