import { CommentOutlined, UnderlineOutlined } from "@ant-design/icons";
import { fabric } from "fabric";
import { useEffect, useRef, useState } from "react";
import ColorPicker from "./ColorPicker";
import styles from "./Submenu.module.css"

import * as common from "./common"
import {
    AlignLeftOutlinedIcon, UnderlineOutlinedIcon
    , AlignCenterOutlinedIcon, AlignRightOutlinedIcon, HighlightTwoToneIcon
    , BoldOutlinedIcon, ItalicOutlinedIcon, FileAddTwoToneIcon, LineHeightOutlinedIcon
} from "../icons/icons";

export default function TextBoxSubmenu({ canvas, addLayerItem }) {

    const color = useRef('#000000');

    function mouseDownHandler(o) {
        const pointer = canvas.getPointer(o.e);
        var textbox = new fabric.Textbox(' ', {
            width: 250,
            fill: `${color.current}`,
            left: pointer.x - 125,
            top: pointer.y - 20,
            id: ++canvas.objectNum,
            type: 'textbox'
        });
     
        canvas.add(textbox);
        common.addLayer(canvas,textbox)
        canvas.setActiveObject(textbox)
        common.updateStates(canvas);
        canvas.defaultCursor = 'default';
    }

    function mouseUpHandler() {
        canvas.off('mouse:down', mouseDown);
    }

    var mouseDown;

    function addTextBox() {
        canvas.defaultCursor = 'text';
        canvas.on('mouse:down', mouseDown = (o) => mouseDownHandler(o));
        canvas.on('mouse:up', mouseUpHandler);
    }

    function makeTextBold() {
        var objects = canvas.getActiveObjects();
        if (objects) {
            objects.forEach((object) => {
                if (object.type === 'textbox') {
                    var fontWeight = object.fontWeight === 'bold' ? 'normal' : 'bold';
                    object.set("fontWeight", fontWeight);
                    console.log(fontWeight)
                    canvas.renderAll();
                    if(fontWeight==='normal') document.querySelector('[aria-label="bold"]').style.background  = 'transparent'
                    else document.querySelector('[aria-label="bold"]').style.background  = 'white'
                }
            })
            common.updateStates(canvas)
        }
    }


    function italicizeText() {
        var objects = canvas.getActiveObjects();
        if (objects) {
            objects.forEach((object) => {
                if (object.type === 'textbox') {
                    var fontFamily = object.fontStyle === 'italic' ? 'normal' : 'italic';
                    object.set("fontStyle", fontFamily);
                    if(fontFamily==='normal') document.querySelector('[aria-label="italic"]').style.background  = 'transparent'
                    else document.querySelector('[aria-label="italic"]').style.background  = 'white';            
                    canvas.renderAll();
                    
                }
            })

            common.updateStates(canvas)
        }
    }


    //
    function underlineText() {
        if (canvas.getActiveObject()) {
            var objects = canvas.getActiveObjects();
            objects.forEach((object) => {
                if (object.type === 'textbox') {
                    var underline = !object.underline;
                    object.set('underline', underline);
                    if(underline===false) document.querySelector('[aria-label="underline"]').style.background  = 'transparent'
                    else document.querySelector('[aria-label="underline"]').style.background  = 'white';    
                    canvas.renderAll();
                }
            })
            common.updateStates(canvas)
        }
    }

    //  
    const alignText = (to) => {
        var objects = canvas.getActiveObjects();
        if (objects) {
            objects.forEach((object) => {
                if (object.type === 'textbox') {
                    object.set({ textAlign: `${to}` });
                    document.querySelector('[aria-label="align-center"]').style.background='transparent';                    
                    document.querySelector('[aria-label="align-left"]').style.background='transparent';                    
                    document.querySelector('[aria-label="align-right"]').style.background='transparent';      
                    
                    var align = 'align-'+to;
                    document.querySelector(`[aria-label="align-${to}"]`).style.background='white';      
                    canvas.renderAll();
               
                }
            })
            common.updateStates(canvas)
        }
    }





    return (
        <div id='text-menu' className={styles.Submenu}>
            <div className={styles.layout}>
            <p><FileAddTwoToneIcon id='add-textbox' onClick={addTextBox} /></p>
            <p><label style={{ marginRight: "10px"}}> </label> <ColorPicker canvas={canvas} color={color} /></p>
            <label style={{ marginLeft: "15px" ,marginTop:"10px"}} > </label>

            <ul>
                <li>
                    <AlignLeftOutlinedIcon onClick={() => alignText('left')}  />
                </li>
                <li>
                    <AlignCenterOutlinedIcon onClick={() => alignText('center')} />
                </li>
                <li>
                    <AlignRightOutlinedIcon onClick={() => alignText('right')}  />
                </li>
            </ul>
            <label style={{ marginLeft: "15px" ,marginTop:"10px"}}></label>
            <ul>
                <li>
                    <BoldOutlinedIcon  onClick={makeTextBold} />
                </li>
                <li>
                    <ItalicOutlinedIcon onClick={italicizeText} />
                </li>
                <li>
                    <UnderlineOutlinedIcon onClick={underlineText} />
                </li>
            </ul>
            </div>
        </div>
    );
}