import { fabric } from "fabric";
import React, { useState, useEffect, useRef } from "react";
import backgroundImage from './img/background.png'
import styles from './Editor.module.css';
import Title from './Title';
import Layer from './Layer';
import Menubar from "./component/Menubar";
import * as common from "./component/submenu/common"


fabric.Object.prototype.getZIndex = function() {
    return this.canvas.getObjects().indexOf(this); //getObjects canvas    .
    //indexOf   fabric.Object index 
}

export default function Editor(props) {//props   .
    const [canvas, setCanvas] = useState(''); //useEffect 
    const[image,setImage] = useState(false); //     
    const imageRef = useRef(false); //https://react.vlpt.us/basic/10-useRef.html   .   menubar setImage()  , Editor.js imageRef   submenu  

    useEffect(() => {  //rendering      
        setCanvas(initCanvas()); // canvas 
    }, []);
    
    //Rightsidebar
    const [Items,setItems]=useState([]); //Items  
    const nextId=useRef(1);
    // var nextId =useRef(canvas.objectNum);

    //TODO:   
    function delItem(canvas,id){
        setItems(//useState  Items  
            Items=>(Items.filter(Item=>Item.id !== id))//Items   id  Items  
            )
        // console.log("objectid:",id);
        canvas.getObjects().forEach(function(object) {//getObjects canvas    . forEach   
            if(object.id === id) {
                canvas.remove(object); // id  canvas  
            }
        })
        canvas.discardActiveObject(); //      
        common.updateStates(canvas); //submenu   
        // canvas.renderAll();
    }
        
    function addLayerItem(canvas,imgSrc){
        //add items
        // console.log("addlayer");
        // let objectcomponent=ReactDOMServer.renderToStaticMarkup(object);
        // console.log("origianlItems: ",Items);
        let newItems=[
            {name:"items"+(canvas.objectNum),//nextId.current
            id:(canvas.objectNum),//nextId.current
            img:imgSrc},
        ...Items]; // Items .
        // console.log("obectNum(id): ",canvas.objectNum);
        // console.log("newItems: ",newItems);
        setItems(newItems); // Items 
        // ++canvas.objectNum;//nextId.current+=1;
    }
    //index=>nexPos  .
    const moveItem =(contents,fromId, toId)=>{//Item,  
        let oriPos= contents.findIndex(obj=>obj.id === Number(fromId)); //Number   
        let newPos=contents.findIndex(obj=>obj.id === Number(toId)); // id   id  
        const newContents=[...contents]; //contents    
        if(newPos<=0){ //  0  0 
        newPos = 0;
        }
        console.log("oriPos",oriPos);
        console.log("newPos",newPos);
        //3=>0
        let moveStep=oriPos-newPos; //  

        newContents.splice(oriPos,1); //oriPos index 1  
        newContents.splice(newPos,0,contents[oriPos]); //newPos index 
        setItems(newContents); //Items 

        //,   
        let objects = canvas.getObjects(); //getObjects canvas    .
        let obj;
        console.log(typeof(fromId));
        console.log(typeof(objects[0].id));
        for (var i = 0; i < objects.length; i++) {//objects 
            if (Number(objects[i].id) == fromId) {
                obj=objects[i];
                console.log(obj);
                if(moveStep>0){// 
                    for(i=0;i<moveStep;i++){
                        canvas.bringForward(obj);
                        console.log(obj);
                    }
                }else{// 
                    for(i=0;i<(moveStep*-1);i++){
                        canvas.sendToBack(obj);
                    }
                }
                break
            }
        }
        // canvas.setActiveObject(obj);
        // canvas.renderAll(); 
    }

    useEffect(()=>{
        if (canvas) {
            
            fabric.Image.fromURL(backgroundImage,(img)=>{//URL  .   
                img.default = true;
                canvas.setBackgroundImage(img,canvas.renderAll.bind(canvas),{//   
                });//bind this  canvas   
                canvas.renderAll(); //top canvas     
                common.updateStates(canvas); //canvas  
            })

            canvas.componentSize = common.initialComponentSize(); //    canvas componentSize  
            common.setCanvasCenter(canvas);
            canvas.zoomInfo =1; //   x1
            
            window.onkeydown=(e)=>common.keyDownEvent(canvas,e) //   , delete, backspace  

            var flag;
            window.addEventListener("resize", function (opt) { //  resize   
                common.setCanvasCenter(canvas)
                var innerWidth = common.getInnerSize(canvas)['innerWidth'];
                var innerHeight = common.getInnerSize(canvas)['innerHeight'];

                var currentWidth = common.getCanvasStyleWidth();
                var currentHeight = common.getCanvasStyleHeight();

                var ratio = canvas.width / canvas.height;

                if (innerWidth < currentWidth) {
                    flag = 'width';
                    common.setCanvasStyleSize(innerWidth, innerWidth * (1 / ratio))
                }
                else if (innerHeight < currentHeight) {
                    flag = 'height'
                    common.setCanvasStyleSize(innerHeight * ratio, innerHeight)
                } else {
                    if (currentHeight < canvas.height && flag === 'height')
                        common.setCanvasStyleSize(innerHeight * ratio, innerHeight)

                    if (currentWidth < canvas.width && flag === 'width')
                        common.setCanvasStyleSize(innerWidth, innerWidth * (1 / ratio))
                }

            });


            canvas.on({
                'mouse:wheel': (opt) => {//  
                    var delta = opt.e.deltaY;
                    var value = delta<0? 0.1:-0.1;
                    canvas.zoomInfo += value;
                    common.zoom(canvas,1+value)
                    // document.getElementById('zoom-info').value =Math.fl oor(canvas.zoomInfo*100) + '%'
                }
            })

        }
    },[canvas])
   
    const initCanvas = () => {
        return (
        new fabric.Canvas('canvas', {
            width: 750,
            height: 500,
            initialWidth: 750,
            initialHeight:500, 
            objectNum : 0,
            undoStack :[],
            redoStack :[],
            componentSize:'',
        })
    )
    }
    

    return (
        <div className={styles.layout}>
            <div  className={styles.center}>
            <Title />
                <div className={styles.mainContainer}>
                    <canvas id="canvas"/>
                    <Layer canvas={canvas}></Layer>
                    <div id="layer"></div>
                </div>
                {canvas && <Menubar canvas={canvas} 
                    image={image} 
                    setImage={setImage} 
                    imageRef={imageRef}
                    addLayerItem={addLayerItem}
                    />}
            </div>
        </div>
    );
}