//https://ant.design/components/icon/#components-icon-demo-custom

export const CheckSquareTwoToneIcon=({onClick, children, htmlFor})=>{
    return(
        <span className={styles.container}>
            <label htmlFor={htmlFor}>
            <CheckSquareTwoTone className={styles.icon} onClick={onClick}/>
            <span className={styles.tooltiptext}>{children}</span>
            </label>
        </span>
    )
}

