import { fabric } from "fabric";
import {
    DeleteTwoToneIcon, PictureTwoToneIcon, CloudTwoToneIcon,
    FolderOpenTwoToneIcon, RightCircleTwoToneIcon, LeftCircleTwoToneIcon, FileZipTwoToneIcon,
    PlusSquareTwoToneIcon, CopyTwoToneIcon, DeleteOutlinedIcon,
    PlusCircleTwoToneIcon, MinusCircleTwoToneIcon,
    FileAddTwoToneIcon, MenuOutlinedIcon, BuildTwoToneIcon
    , ControlTwoToneIcon, EditTwoToneIcon, LayoutTwoToneIcon
} from "./icons/icons"
import styles from "./Menubar.module.css"
import backgroundImage from '../img/background.png'
import * as common from './submenu/common'
//
import FigureSubmenu from "./submenu/FigureSubmenu";
import TextBoxSubmenu from "./submenu/TextBoxSubmenu";
import FilterSubmenu from "./submenu/FilterSubmenu";
import LineSubmenu from "./submenu/LineSubmenu";
import CropSubmenu from "./submenu/CropSubmenu";

import { useEffect, useRef, useState } from "react";

export default function Menubar(props) {
    const canvas = props.canvas;

    useEffect(() => {
        document.onkeydown = function (e) {
            if (e.ctrlKey && e.shiftKey && e.key === 'Z') {
                redo();
            } else if (e.ctrlKey && e.key === 'z') {
                undo();
            } else if (e.ctrlKey && e.key === 'c') {
                copy();
            } else if (e.ctrlKey && e.key === 'v') {
                paste();
            }
        }
        canvas.on({
            'selection:cleared':(e)=>{
              window.onkeydown = (e) => common.keyDownEvent(canvas, e);
              common.colorActiveLayer(canvas)
              if (e.deselected)
              e.deselected.forEach((object)=>{if (!object.cropRect)common.modifyLayer(object)});
            },
            'object:added': (e) => {
              // canvas.setActiveObject(e.target)
              // if(e.target.type==='path') canvas.discardActiveObject();
            },
            'object:modified':(e)=>{
              if(e.target._objects){
                var group= e.target;
                group.forEachObject((object)=>{
                  object.canvasRelativePosition = {'left':group.left+object.left+group.width/2, 'top': group.top+object.top+group.height/2};
                  common.modifyLayer(object)
                })
              }else{
                common.modifyLayer(e.target)
                
              }
              
                if(!e.target.cropRect) common.updateStates(canvas);
            },
            'selection:updated': (e) => {
              setMenu(common.getMenuType(e.selected[0]), true)
              common.inputObjectInfo(e.selected[0])
              common.colorActiveLayer(canvas);
              props.canvas.getObjects().forEach(object=>{
                console.log(object.getZIndex())
              })
            },
            'selection:created': (e) => {
              common.colorActiveLayer(canvas)
              if(e.selected.length!==1) return ; //     menu    
        
              if(e.selected[0].cropRect || e.selected[0].main ) return ;
              setMenu(common.getMenuType(e.selected[0]), true)
              common.inputObjectInfo(e.selected[0])
            },
            'object:scaling': (e) => {
              common.inputObjectInfo(e.target)
            },
          })

    }, []);


    function importImage(e) {
        e.target.value = '' //        
        var innerWidth = common.getInnerSize(canvas)['innerWidth'];
        var innerHeight = common.getInnerSize(canvas)['innerHeight'];

        document.getElementById('import-image-file').onchange = function (e) {
            // common.initalCanvas(canvas);
            var file = e.target.files[0];
            var reader = new FileReader();

            reader.onload = function (f) {
                canvas.undoStack = []
                canvas.objectNum = 0
                props.setImage(!props.image);
                props.imageRef.current = true;

                var data = f.target.result;
                fabric.Image.fromURL(data, function (img) {
                    common.removeAllObjects(canvas, true)
                    canvas.setWidth(img.width);
                    canvas.setHeight(img.height);
                    img.main = true;
                    img.selectable = false;
                    // // img.evented =false;
                    // img.hasBorders=false;
                    // img.hasControls=false;
                    canvas.add(img)
                    canvas.discardActiveObject(img);
                    canvas.renderAll();
                    // canvas.setBackgroundImage(backgroundImage); 
                    canvas.initialWidth = img.width;
                    canvas.initialHeight = img.height

                    if (img.width > innerWidth || img.height > innerHeight) common.fitToProportion(canvas)

                    common.setCanvasCenter(canvas);
                    common.updateStates(canvas);
                    canvas.renderAll();

                    common.setMenu('filter-menu',canvas,false)

                    //      
                    const divElem = document.getElementById('filter-menu');
                    const inputElements = divElem.querySelectorAll("input[type=range], input[type=checkbox], button")
                    for (var i = 0; i < inputElements.length; i++) {
                        inputElements[i].disabled = false;
                    }

                });
            };
            reader.readAsDataURL(file);
        };
    }

    function clearCanvas() { // 
        common.removeAllObjects(canvas, true);
        common.initalCanvas(canvas);
        common.updateStates(canvas);
        common.setCanvasCenter(canvas);
        canvas.renderAll();

    }

    // 
    function downloadImage() {
        if (canvas.backgroundImage.default)
            canvas.clone((clonedCanvas) => {
                clonedCanvas.setBackgroundImage(null, () => {
                    var image = clonedCanvas.toDataURL();  //canvas    
                    var link = document.createElement('a'); //<a> 
                    link.href = image;
                    link.download = "image.png";
                    link.click();
                })
            });
        else {
            var image = canvas.toDataURL({
                crossOrigin: 'anonymous'
            });  //canvas    
            var link = document.createElement('a'); //<a> 
            link.href = image;
            link.download = "image.png";
            link.click();
        }
    }

    const serializationJson = () => {
        var json = canvas.toDatalessJSON(['initialWidth', 'initialHeight', 'objectNum', 'id', 'filterValues', 'main']);
        json = JSON.stringify(json);
        window.localStorage.setItem("userJson", json);
        return json;
    }

    //  
    function serialization() {
        var json = serializationJson();
        var blob = new Blob([json], { type: "text/plain;charset=utf-8" });
        var link = document.createElement('a'); //<a> 

        link.href = URL.createObjectURL(blob);
        link.download = "image.json";
        link.click();
    }

    const DeserializationJson = (Json) => {
        canvas.loadFromJSON(Json, () => {
            common.removeAllLayer(canvas)
            common.initalCanvas(canvas, true);
            canvas.setWidth(canvas.initialWidth);
            canvas.setHeight(canvas.initialHeight);
            common.setCanvasCenter(canvas);
            common.updateStates(canvas);
            var Objects = canvas.getObjects();
            Objects.forEach((object) => {
                if (!object.main){
                    common.addLayer(canvas, object);
                }
            })
            canvas.discardActiveObject(common.getMainImage())
            common.colorActiveLayer(canvas);
            canvas.renderAll();
        });
    }
    // 
    function Deserialization(e) {
        e.target.value = '' //        
        document.getElementById("Deserialization-json-file").onchange = function (e) {
            var reader = new FileReader();
            reader.onload = function (e) { //onload():     
                DeserializationJson(reader.result);
            }
            reader.readAsText(e.target.files[0]); // dataURL   
        }
    }
    const loadData = () => {
        const userJson = window.localStorage.getItem("userJson");
        DeserializationJson(userJson);
    }


    function undo() {
        if (canvas.undoStack.length > 1) {
            canvas.discardActiveObject();
            common.removeAllLayer(canvas);
            common.removeAllObjects(canvas);
            var current = canvas.undoStack.pop();
            canvas.redoStack.push(current);
            var json = canvas.undoStack[canvas.undoStack.length - 1];
            var objects = json['objects'];
            
            //crop   undo   
            if (json['recentStyleSize']) {
                canvas.remove(common.getMainImage(canvas))
                canvas.setWidth(json['initialWidth']);
                canvas.setHeight(json['initialHeight']);
                if (json['recentStyleSize']['mainImage']) {
                    canvas.add(json['recentStyleSize']['mainImage']);
                    canvas.discardActiveObject(json['recentStyleSize']['mainImage']);
                }
                // common.setCanvasStyleSize(json['recentStyleSize']['width'], json['recentStyleSize']['height']);
                common.setCanvasCenter(canvas);
            }


            objects.forEach((object) => {
                if (object.main) {
                    object.set({ selectable: false })
                }
                if (object.main !== true && !object.cropRect) {
                    canvas.add(object)
                    common.addLayer(canvas,object);
                }
                if (object.canvasRelativePosition) {
                    object.left = object.canvasRelativePosition['left']
                    object.top = object.canvasRelativePosition['top']
                }

            })

            var filters = json['filters'];
            var mainImage = json['image'];
            if (mainImage) mainImage.applyFilters(filters);

            canvas.renderAll();

            common.setCanvasCenter(canvas);
         
            try { 
                var state = json['filterRangeState'];
                var inputNodes = document.getElementById('filter-list').getElementsByTagName('input');
                if (!state) {
                    for (var i = 0; i < inputNodes.length; i++) {
                        inputNodes[i].checked = false;
                        inputNodes[i].value = inputNodes[i].defaultValue;
                    }

                }
                for (var j = 0; j < inputNodes.length; j++) {

                    var id = inputNodes[j].id;
                    if (inputNodes[j].type === 'checkbox') {
                        document.getElementById(id).checked = state[0][id];
                    } else if (inputNodes[j].type === 'range') {

                        document.getElementById(id).value = Number(state[1][id]);
                    } else {
                        document.getElementById(id).value = state[2][id];
                    }

                }
            } catch (e) {
            }

        }
    }

    function redo() {
        if (canvas.redoStack.length > 0) {
            common.removeAllLayer(canvas);
            common.removeAllObjects(canvas);
            var json = canvas.redoStack.pop();
            canvas.undoStack.push(json);

            var objects = json['objects'];
            var filters = json['filters']
            objects.forEach((object) => {
                if (!object.main && !object.cropRect) {
                    if (object.canvasRelativePosition) {
                        object.left = object.canvasRelativePosition['left']
                        object.top = object.canvasRelativePosition['top']
                    }
                    canvas.add(object)
                    common.addLayer(canvas,object)
                }
            })

            if (common.getMainImage(canvas)) common.getMainImage(canvas).applyFilters(filters);
            canvas.renderAll();
            if (json['isCropped']) {
                common.removeAllObjects(canvas, true);
                canvas.setWidth(json['initialWidth']);
                canvas.setHeight(json['initialHeight']);
                canvas.add(json['image']);
                canvas.discardActiveObject(json['image']);
                common.setCanvasCenter(canvas);
                canvas.renderAll();
            }

            try {
                var state = json['filterRangeState'];
                var inputNodes = document.getElementById('filter-list').getElementsByTagName('input');
                if (!state) {
                    for (var i = 0; i < inputNodes.length; i++) {
                        inputNodes[i].checked = false;
                        inputNodes[i].value = inputNodes[i].defaultValue;
                    }
                }
                for (var j = 0; j < inputNodes.length; j++) {
                    var id = inputNodes[j].id;
                    if (inputNodes[j].type === 'checkbox') {
                        document.getElementById(id).checked = state[0][id];
                    } else if (inputNodes[j].type === 'range') {
                        document.getElementById(id).value = Number(state[1][id]);
                    } else {
                        document.getElementById(id).value = state[2][id];
                    }

                }
            } catch (e) { }
        }
    }

    var _clipboard;
    function copy() {
        if (canvas.getActiveObject())
            canvas.getActiveObject().clone(function (cloned) {
                _clipboard = cloned;
            });
    }

    function paste() {
        if (_clipboard)
            _clipboard.clone(function (clonedObj) {
                clonedObj.set({
                    left: clonedObj.left + 10,
                    top: clonedObj.top + 10,
                    evented: true,
                });
                if (clonedObj.type === 'activeSelection') {
                    clonedObj.canvas = canvas;
                    clonedObj.destroy()

                    clonedObj.forEachObject(function (obj) {

                        obj.set({
                            id: ++canvas.objectNum,
                        })

                        canvas.add(obj);
                        canvas.setActiveObject(obj);
                        common.addLayer(canvas, obj);
                       
                        common.colorActiveLayer(canvas);
                        common.updateStates(canvas);
                    });
                    canvas.renderAll();
                   
                    clonedObj.setCoords();
                } else {
                    clonedObj.set({
                        id: ++canvas.objectNum,
                    })
                    canvas.add(clonedObj);
                    canvas.setActiveObject(clonedObj);
                    common.addLayer(canvas, clonedObj);
                  
                    common.colorActiveLayer(canvas);
                    common.updateStates(canvas);
                    canvas.requestRenderAll();
                }

                _clipboard.top += 10;
                _clipboard.left += 10;

           
            });
    }
//f1
const addObjectRef = useRef(false);  
const figure = ['rect', 'triangle', 'circle', 'image'];

function setMenu(type,canvasEvent=false) {
  if(type ==='') return;
  if(!canvasEvent){
    common.mouseEventOff(canvas);
    canvas.isDrawingMode = false; 
  }
  canvas.defaultCursor = 'default';

  var display = document.getElementById(type).style.display;

  if (display === 'block' && !canvasEvent) {
    document.getElementById(type).style.display = '';
    //       
  }
  else {
    var nodes = document.getElementById('submenu').childNodes;
    nodes.forEach(node => node.style.display = '');
    document.getElementById(type).style.display = 'block'
  }
}
//f2

function zoomIn() {
    canvas.zoomInfo +=0.1
    common.zoom(canvas, 1.1);
}
function zoomOut() {
    canvas.zoomInfo -=0.1
    common.zoom(canvas, 0.9);
}
function fitToProportion() {
    common.fitToProportion(canvas);
    common.setCanvasCenter(canvas);
}

if (canvas)
    var recentStyleSize;


    return (
        <div id="menubar" className={styles.editorMenubar}>
            
                <DeleteTwoToneIcon onClick={clearCanvas} children={" "} />
                <CloudTwoToneIcon children={" "} />
                <FolderOpenTwoToneIcon onClick={downloadImage} children={" "} />
                <PictureTwoToneIcon htmlFor="import-image-file" children={" "} />
                <input type="file" id="import-image-file" name="chooseFile" accept="image/*"
                    onClick={importImage} />
                <CopyTwoToneIcon id='copy' onClick={copy} children={""} />
                <PlusSquareTwoToneIcon id='paste' onClick={paste} children={""} />
                {/* */}
                <LeftCircleTwoToneIcon id='undo' onClick={undo} children={" "} />
                {/*   */}
                <RightCircleTwoToneIcon id='redo' onClick={redo} children={" "} />
       {/* f3 */}

          <BuildTwoToneIcon onClick={()=>setMenu('figure-menu')} children={""}/>
          <FileAddTwoToneIcon onClick={()=>setMenu('text-menu')} children={" "}/>
          <EditTwoToneIcon onClick={()=>setMenu('line-menu')} children={" "}/>
          <ControlTwoToneIcon onClick={()=>setMenu('filter-menu')} children={" "}/>
          <LayoutTwoToneIcon onClick={()=>setMenu('crop-menu')} children={""}/>

       <div id='submenu'>   
        <FigureSubmenu canvas={canvas} addLayerItem={props.addLayerItem} />
        <TextBoxSubmenu canvas={canvas}  addLayerItem={props.addLayerItem}/>
        <LineSubmenu canvas={canvas}  addLayerItem={props.addLayerItem}/>
        <FilterSubmenu canvas={canvas} />
        <CropSubmenu canvas={canvas} />
       </div>
        {/* f4 */}
                <h2 className={styles.title}>
                </h2>
                <span></span>
                <PlusCircleTwoToneIcon onClick={zoomIn} children={""} />
                <input type="text" id='zoom-info' ref={props.zoomInfo} defaultValue={'100%'} className={styles.inputdesign} disabled />
                <MinusCircleTwoToneIcon onClick={zoomOut} children={""} />
                <span></span>
                <div id='layer-list' style={{display:"none"}}></div>
    </div>
    )
}